import { useParams } from "react-router-dom";
import { Grid, CircularProgress } from "@material-ui/core";
import { useOwnedTokens, useArweave, TokenInfo } from "../context/Wallet";

export default function Me() {
  const { address } = useParams<{ address: string | undefined }>();
  console.log("using address", address);
  const ownedTokens = useOwnedTokens(address);
  return (
    <Grid container spacing={1}>
      {ownedTokens &&
        ownedTokens.result &&
        ownedTokens.result
          .filter((tokenInfo) => tokenInfo.metadata !== undefined)
          .map((tokenInfo: any, index: number) => (
            <NftGridItem tokenInfo={tokenInfo} key={index} />
          ))}
    </Grid>
  );
}

function NftGridItem({ tokenInfo }: { tokenInfo: TokenInfo }) {
  const arweaveAsync = useArweave(tokenInfo.metadata);
  return !arweaveAsync.result ? (
    <div
      style={{
        width: "33%",
        display: "flex",
        justifyContent: "center",
        flexDirection: "column",
      }}
    >
      <CircularProgress
        style={{
          marginLeft: "auto",
          marginRight: "auto",
          width: "50px",
          height: "50px",
        }}
      />
    </div>
  ) : (
    <Grid item xs={2} sm={4} md={4}>
      <img
        alt={arweaveAsync.result.name}
        style={{ width: "100%" }}
        src={arweaveAsync.result.image}
      />
    </Grid>
  );
}
