import { useState } from "react";
import WalletProvider from "./context/Wallet";
import { WalletMultiButton } from "@solana/wallet-adapter-react-ui";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  useHistory,
} from "react-router-dom";
import { CssBaseline, makeStyles } from "@material-ui/core";
import Me from "./components/Me";
import Match from "./components/Match";
import AnchorProvider from "./context/Anchor";
import "./App.css";

export default function App() {
  return (
    <div className="App">
      <CssBaseline />
      <WalletProvider>
        <AnchorProvider>
          <Router basename="/nft-gallery">
            <div>
              <Header />
              <Switch>
                <Route exact path="/me" component={Me} />
                <Route exact path="/wallet/:address" component={Me} />
                <Route exact path="/match" component={Match} />
              </Switch>
            </div>
          </Router>
        </AnchorProvider>
      </WalletProvider>
    </div>
  );
}

const useStyles = makeStyles(() => ({
  searchBar: {
    "&:focus": {
      width: "600px",
    },
    border: `1pt solid #ccc`,
    backgroundColor: "rgb(249 249 249)",
    width: "400px",
    borderRadius: "8px",
    outline: "none",
    paddingLeft: "10px",
    paddingRight: "10px",
    height: "30px",
    transition: "width .3s",
  },
  homeButton: {
    "&:hover": {
      cursor: "pointer",
    },
  },
}));

function Header() {
  const history = useHistory();
  const classes = useStyles();
  const [searchText, setSearchText] = useState("");
  const onKeyDown = (e: any) => {
    if (e.key === "Enter") {
      history.push(`/wallet/${e.target.value}`);
    }
  };
  const onHomeClick = () => {
    history.push("/me");
  };
  return (
    <div
      style={{
        paddingLeft: "8px",
        paddingRight: "8px",
        backgroundColor: "rgb(105 85 235)",
        display: "flex",
        justifyContent: "space-between",
      }}
    >
      <div style={{ display: "flex" }}>
        <div
          className={classes.homeButton}
          onClick={onHomeClick}
          style={{
            display: "flex",
            justifyContent: "center",
            flexDirection: "column",
          }}
        >
          <img
            style={{ height: "45px" }}
            src="https://dao.projectserum.com/static/media/dark-horizontal-combined-rainbow.inline.4abc2aec.svg"
          />
        </div>
        <div
          style={{
            marginLeft: "8px",
            display: "flex",
            justifyContent: "center",
            flexDirection: "column",
          }}
        >
          <input
            value={searchText}
            onKeyDown={onKeyDown}
            onChange={(e) => setSearchText(e.target.value)}
            placeholder="Search..."
            className={classes.searchBar}
          />
        </div>
      </div>
      {/*
			<div>
				<Button>
					<Link to="/me">
						Me
					</Link>
				</Button>
				<Button>
					<Link to="/match">
						Match
					</Link>
				</Button>
			</div>
				*/}
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          flexDirection: "column",
        }}
      >
        <WalletMultiButton
          style={{
            height: "35px",
            backgroundColor: "#f35757",
            borderRadius: 10,
          }}
        />
      </div>
    </div>
  );
}
