import { useMemo } from "react";
import { useAsync, UseAsyncReturn } from "react-async-hook";
import * as anchor from "@project-serum/anchor";
import { PublicKey } from "@solana/web3.js";
import {
  ConnectionProvider,
  WalletProvider,
} from "@solana/wallet-adapter-react";
import { WalletAdapterNetwork } from "@solana/wallet-adapter-base";
import {
  getPhantomWallet,
  getSolflareWallet,
  getSolletWallet,
} from "@solana/wallet-adapter-wallets";
import { WalletModalProvider } from "@solana/wallet-adapter-react-ui";
import { clusterApiUrl } from "@solana/web3.js";
import { getOwnedTokenAccounts } from "../utils/tokens";
import { useAnchor } from "./Anchor";
import { metadata } from "@project-serum/token";
require("@solana/wallet-adapter-react-ui/styles.css");

export default function Wallet(props: any) {
  const network = WalletAdapterNetwork.Mainnet;
  const endpoint = useMemo(() => clusterApiUrl(network), [network]);
  const wallets = useMemo(
    () => [
      getPhantomWallet(),
      getSolflareWallet(),
      getSolletWallet({ network }),
    ],
    [network]
  );

  return (
    <ConnectionProvider endpoint={endpoint}>
      <WalletProvider wallets={wallets} autoConnect>
        <WalletModalProvider>{props.children}</WalletModalProvider>
      </WalletProvider>
    </ConnectionProvider>
  );
}

export function useOwnedTokens(
  ownerAddr?: string
): UseAsyncReturn<Array<TokenInfo>> {
  const { provider } = useAnchor();
  return useAsync<Array<TokenInfo>>(async () => {
    const owner = ownerAddr
      ? new PublicKey(ownerAddr)
      : provider.wallet
      ? provider.wallet.publicKey
      : undefined;
    if (owner === undefined) {
      return [];
    }
    const ts = readOwnedTokens(owner);

    // Perform the fetch if needed.
    if (!ts) {
      setOwnedTokens(
        owner,
        new Promise<Array<PublicKey>>(async (ownedTokensResolve) => {
          // Fetch and cache all tokens for this user.
          const tokens = await getOwnedTokenAccounts(
            provider.connection,
            // @ts-ignore
            owner
          );
          tokens.forEach((t: any) => {
            setToken(t.publicKey, new Promise((resolve) => resolve(t)));
          });

          // Calculate all the metadata.
          const metadataPubkeys = await Promise.all(
            // @ts-ignore
            tokens.map((t) => getMetadata(t.account.mint))
          );

          // Fetch and cache all metadata accounts.
          const accs = await Promise.all(
            (
              await anchor.utils.rpc.getMultipleAccounts(
                provider.connection,
                metadataPubkeys
              )
            )
              .filter((m) => m !== null)
              .map((m) => ({
                ...m,
                account: metadata.decodeMetadata(m!.account.data),
              }))
          );
          accs.forEach((metadata) => {
            setMetadata(
              //@ts-ignore
              metadata.account.mint,
              //@ts-ignore
              new Promise<Metadata>((resolve) => resolve(metadata))
            );
          });

          // Done.
          // @ts-ignore
          ownedTokensResolve(tokens.map((t) => t.publicKey));
        })
      );
    }

    // Use the cache to construct the result.
    const ownedTokens = await readOwnedTokens(owner);
    const result: Array<TokenInfo> = await Promise.all(
      ownedTokens!.map(async (tAddr) => {
        const data = await readToken(tAddr);
        const metadata = await readMetadata(data!.account.mint);
        return {
          data: data as Token,
          metadata: metadata as Metadata,
        };
      })
    );
    console.log("returning result", result);
    return result;
  }, [provider, ownerAddr]);
}

export function useArweave(md: Metadata): UseAsyncReturn<Arweave> {
  return useAsync<Arweave>(async () => {
    const data = readArweave(md.account.data.uri);
    if (!data) {
      setArweave(
        md.account.uri,
        new Promise<Arweave>((resolve, reject) => {
          fetch(md.account.data.uri)
            .then((resp) => resp.json())
            .then(resolve)
            .catch(reject);
        })
      );
    }
    return await readArweave(md.account.uri);
  }, [md]);
}
export function readArweave(uri: string): Promise<Arweave> | undefined {
  return ARWEAVE_CACHE.get(uri);
}
export function setArweave(uri: string, nft: Promise<Arweave>) {
  ARWEAVE_CACHE.set(uri, nft);
}

export function readOwnedTokens(
  owner: PublicKey
): Promise<Array<PublicKey>> | undefined {
  return OWNED_TOKENS_CACHE.get(owner.toString());
}

export function setOwnedTokens(owner: PublicKey, t: Promise<Array<PublicKey>>) {
  OWNED_TOKENS_CACHE.set(owner.toString(), t);
}

export function readToken(mint: PublicKey): Promise<Token> | undefined {
  return TOKEN_CACHE.get(mint.toString());
}

export function setToken(mint: PublicKey, nft: Promise<Token>) {
  TOKEN_CACHE.set(mint.toString(), nft);
}

export function readMetadata(mint: PublicKey): Promise<Metadata> | undefined {
  return METADATA_CACHE.get(mint.toString());
}

export function setMetadata(mint: PublicKey, md: Promise<Metadata>) {
  METADATA_CACHE.set(mint.toString(), md);
}

// Maps user wallet to array of tokens it owns.
const OWNED_TOKENS_CACHE = new Map<string, Promise<Array<PublicKey>>>();

// Maps token address to token data.
const TOKEN_CACHE = new Map<string, Promise<Token>>();

// Maps token address to token metadata.
const METADATA_CACHE = new Map<string, Promise<Metadata>>();

// Maps metadata uri to arweave data.
const ARWEAVE_CACHE = new Map<string, Promise<Arweave>>();

export type Token = { publicKey: PublicKey; account: any };
export type Metadata = { publicKey: PublicKey; account: any };
export type TokenInfo = { data: Token; metadata: Metadata };
export type Arweave = any;

const TOKEN_METADATA_PROGRAM_ID = new PublicKey(
  "metaqbxxUerdq28cj1RbAWkYQm3ybzjb6a8bt518x1s"
);

async function getMetadata(mint: PublicKey): Promise<PublicKey> {
  return (
    await PublicKey.findProgramAddress(
      [
        Buffer.from("metadata"),
        TOKEN_METADATA_PROGRAM_ID.toBuffer(),
        mint.toBuffer(),
      ],
      TOKEN_METADATA_PROGRAM_ID
    )
  )[0];
}
