import { useMemo, createContext, useContext } from "react";
import { Program, Provider } from "@project-serum/anchor";
import { useAnchorWallet } from "@solana/wallet-adapter-react";
import { NftSwap, IDL } from "../idls/nft-swap";
import { PublicKey, Connection, ConfirmOptions } from "@solana/web3.js";

type AnchorContext = {
  provider: Provider;
  nftSwap: Program<NftSwap> | undefined;
};

const _AnchorContext = createContext<AnchorContext | null>(null);

export default function AnchorProvider(props: any) {
  const wallet = useAnchorWallet();
  const provider = useMemo(() => {
    const connection = new Connection(
      //			'https://solana-api.projectserum.com',
      "https://api.mainnet-beta.solana.com",
      "recent"
    );
    const opts: ConfirmOptions = {
      preflightCommitment: "recent",
      commitment: "recent",
    };
    // @ts-ignore
    const provider = new Provider(connection, wallet, opts);
    return provider;
  }, [wallet]);
  const { nftSwap } = useMemo(() => {
    const programId = new PublicKey(
      "2joi9QJe3GN55vkRhUtMNU9QN4zKmBJDH8Y2zJ8d4gs4"
    );
    const nftSwap = new Program<NftSwap>(IDL, programId, provider);
    return { nftSwap, provider };
  }, [provider]);
  return (
    <_AnchorContext.Provider
      value={{
        provider,
        nftSwap,
      }}
    >
      {props.children}
    </_AnchorContext.Provider>
  );
}

export function useAnchor() {
  const ctx = useContext(_AnchorContext);
  if (ctx === null) {
    throw new Error("Context not available");
  }
  return ctx;
}
